  /* BOTONES */
  
  /* COLORES BOTONES */
  :root {
    --clr-neon: #ff9914;
    --clr-bg: rgb(37, 37, 37);
    --clr-bl: rgb(75, 255, 20);
    --clr-bm: rgb(169, 20, 255);
    --clr-ba: #143fff;
    --clr-br: rgb(255, 20, 59);
    --clr-bs: rgb(231, 255, 20);
  }
  
  
  /* ANIMACIONES GET STARTED */
  
  .svg {
    display: flex;
    flex-wrap: wrap;
    width: 90px;
    position: relative;
    /* margin-top: -10%;
    margin-left: 85%; */
    pointer-events: none;
  }
  
  /* BOTON GET STARTED */
  .started-button {
    font-size: 1vmax;
    flex-wrap: wrap;
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
    color: var(--clr-bs);
    border: var(--clr-bs) 0.125em solid;
    padding: 0.25em 1em;
    border-radius: 0.25em;
    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em currentColor;
    box-shadow: inset 0 0 0.5em 0 var(--clr-bs), 0 0 0.5em 0 var(--clr-bs);
    position: relative;
    background-color: rgba(0, 0, 0, 0);
    margin: 7px;
  }
  
  .started-button::before {
    pointer-events: none;
    content: "";
    position: absolute;
    background: var(--clr-bs);
    top: 120%;
    left: 0;
    width: 100%;
    height: 100%;
  
    transform: perspective(1em) rotateX(40deg) scale(1, 0.35);
    filter: blur(1em);
    opacity: 0.7;
  }
  
  .started-button::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow: 0 0 2em 0.5em var(--clr-bs);
    opacity: 0;
    background-color: var(--clr-bs);
    z-index: -1;
    transition: opacity 100ms linear;
  }
  
  .started-button:hover,
  .started-button:focus {
    color: var(--clr-bg);
    text-shadow: none;
  }
  
  .started-button:hover::before,
  .started-button:focus::before {
    opacity: 1;
  }
  .started-button:hover::after,
  .started-button:focus::after {
    opacity: 1;
  }
  
