    height: 2px;
    background: linear-gradient(90deg, transparent, var(--clr-bl));
  }
  
  
  
  .btn-cart:hover span:nth-child(1) {
    left: 100%;
    transition: 1s;
  }
  
  
  .btn-cart span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, var(--clr-bl));
  }
  
  .btn-cart:hover span:nth-child(3) {
    right: 100%;
    transition: 1s;
    transition-delay: 0.5s;
  }
  
  
  
  .btn-cart span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, var(--clr-bl));
  }
  
  .btn-cart:hover span:nth-child(2) {
    top: 100%;
    transition: 1s;
    transition-delay: 0.25s;
  }
  
  
  
  .btn-cart span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, var(--clr-bl));
  }
  
  .btn-cart:hover span:nth-child(4) {
    bottom: 100%;
    transition: 1s;
    transition-delay: 0.75s;
  }
  
  
